<template>
  <div style="background: white;margin: auto;">
    <apexchart type=area width="100%" :height="height" :options="chartOptions" :series="series"/>
  </div>
</template>

<script>
import util from './utils'

export default {
  name: 'Area',
  props: {
    height: {
      default: 400,
      type: Number
    }
  },
  data () {
    return {
      series: [{
        name: 'series1',
        data: [31, 40, 28, 51, 42, 109, 100]
      }, {
        name: 'series2',
        data: [11, 32, 45, 32, 34, 52, 41]
      }],
      chartOptions: {
        chart: {
          toolbar: {
            show: false
          }
        },
        colors: util.Color.getColors(),
        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: 'smooth'
        },
        xaxis: {
          type: 'datetime',
          categories: [
            '2018-09-19T00:00:00', '2018-09-19T01:30:00', '2018-09-19T02:30:00',
            '2018-09-19T03:30:00', '2018-09-19T04:30:00', '2018-09-19T05:30:00',
            '2018-09-19T06:30:00'
          ]
        },
        tooltip: {
          x: {
            format: 'dd/MM/yy HH:mm'
          }
        }
      }
    }
  }
}
</script>

<style scoped>

</style>



