// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router/lazy'
import 'ant-design-vue/dist/antd.css'
import Antd from 'ant-design-vue'
import Viser from 'viser-vue'
import axios from 'axios'
import '@/mock'
import store from './store'
import PouchDB from 'pouchdb'
import VueApexCharts from 'vue-apexcharts'
import VCharts from 'v-charts'
// g2 已加入
// fusionchart
import VueFusionCharts from 'vue-fusioncharts'
import FusionCharts from 'fusioncharts'
import Charts from 'fusioncharts/fusioncharts.charts'
// import the theme fusion gammel
import FusionTheme from 'fusioncharts/themes/fusioncharts.theme.fusion'
// highcharts
import Highcharts from 'highcharts'
import HighchartsVue from 'highcharts-vue'

// apex 版
// import CustomChart from './pages/charts/apex/index'
// v-charts 版
import CustomChart from './pages/charts/v-charts/index'

Vue.prototype.$axios = axios
Vue.config.productionTip = false
Vue.use(Viser)
Vue.use(Antd)
Vue.use(VCharts)
Vue.use(VueApexCharts)
Vue.use(VueFusionCharts, FusionCharts, Charts, FusionTheme)
Vue.use(CustomChart)

Vue.component('apexchart', VueApexCharts)
// Vue.component('highcharts', HighchartsVue)
// 全局设置
Highcharts.setOptions({
  colors: ['#1890FF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864', '#8543E0', '#3436C7', '#223273', '#058DC7', '#50B432', '#ED561B', '#DDDF00', '#24CBE5', '#64E572', '#FF9655', '#FFF263', '#6AF9C4']
})

Vue.use(HighchartsVue, {
  highcharts: Highcharts
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  mounted () {
    var db = new PouchDB('admindb')
    db.get('currUser').then(doc => {
      this.$store.commit('account/setuser', doc.user)
    })
  }
})



