import Area from '../apex/Area'
import Bar from './Bar'
import Column from './Column'
import Donut from './Donut'
import Line from './Line'
import Pie from './Pie'

import Vue from 'vue'

// 基于 echarts 的封装
export default {
  install: function () {
    Vue.component('c-area', Area)
    Vue.component('c-bar', Bar)
    Vue.component('c-column', Column)
    Vue.component('c-donut', Donut)
    Vue.component('c-line', Line)
    Vue.component('c-pie', Pie)
  }
}



