let util = {}
util.Color = {
  getColors (type) {
    let map = {
      // viser 配色方案
      // default: ['#3977FF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864', '#8543E0'],
      // line: ['#3977FF', '#FA8C15', '#13C2C2', '#2FC25B', '#FACC14', '#F04864', '#8543E0']
      // antv +杉德 配色方案
      default: ['#1890FF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864', '#8543E0', '#3436C7', '#223273'],
      loudou: ['#1890FF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864', '#8543E0', '#3436C7', '#223273']
    }
    return map[type || 'default'] || map['default']
  }
}

export default util



